footer {
  background: #4f585e;
}
footer div.socialMediaLinks {
  background: #323b40;
  overflow: hidden;
  padding-bottom: 4px;
  text-align: center;
}
footer div.socialMediaLinks ul {
  margin: 0;
  padding: 0;
}
footer div.socialMediaLinks li {
  display: inline;
  margin: 0;
  padding: 0;
}
footer div.socialMediaLinks a {
  border-bottom: 0px solid rgba(0, 0, 0, 0.95);
  border-radius: 4px;
  box-shadow: inset 0 -3px 0 0 rgba(0, 0, 0, 0), 0 6px 8px rgba(0, 0, 0, 0), 0 24px 24px rgba(0, 0, 0, 0), 0 36px 36px rgba(0, 0, 0, 0), 0 64px 64px rgba(0, 0, 0, 0), 0 64px 128px rgba(0, 0, 0, 0), 0 120px 0 rgba(0, 0, 0, 0), 0 86px 8px 6px rgba(0, 0, 0, 0);
  display: inline-block;
  height: 30px;
  padding: 20px;
  position: relative;
  transition: 0.2s ease-in;
  width: 30px;
}
footer div.socialMediaLinks a svg {
  left: 20px;
  position: absolute;
  top: 20px;
  height: 30px;
  width: 30px;
}
footer div.socialMediaLinks a svg.glow path,
footer div.socialMediaLinks a svg.glow circle {
  fill: rgba(0, 0, 0, 0);
}
footer div.socialMediaLinks a svg path,
footer div.socialMediaLinks a svg circle {
  fill: #ffca18;
  transition: 0.2s ease-in;
}
footer div.socialMediaLinks a:hover {
  transform: translateY(-4px);
  box-shadow: inset 0 -3px 0 0 rgba(0, 0, 0, 0.1), 0 6px 8px rgba(0, 0, 0, 0.05), 0 24px 24px rgba(0, 0, 0, 0.05), 0 36px 36px rgba(0, 0, 0, 0.05), 0 64px 64px rgba(0, 0, 0, 0.15), 0 64px 128px rgba(0, 0, 0, 0.15), 0 86px 8px 6px rgba(255, 219, 102, 0.25), 0 83px 4px 0px rgba(255, 219, 102, 0.95);
}
footer div.socialMediaLinks a:hover svg.glow {
  filter: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg"><fâ€¦ter id="filter"><feGaussianBlur stdDeviation="10" /></filter></svg>#filter');
  filter: blur(2px);
}
footer div.socialMediaLinks a:hover svg.glow path,
footer div.socialMediaLinks a:hover svg.glow circle {
  fill: rgba(255, 219, 102, 0.6);
}
footer div.socialMediaLinks a:hover svg path,
footer div.socialMediaLinks a:hover svg circle {
  fill: #ffdb66;
}
